<template>
    <div class="makemoney">
        <navComponent/>
        <headerVue/> 
        <sectionOne/>
        <sectionTow/>
        <div class="container">
            <div class="notdriveryet">
                <div class="titleeee">
                    Not a driver yet ? Start earning today
                </div>
                <div class="notdrivebutton">
                    <button class="btn btn-light ffsego">JOIN NOW</button>
                </div>
            </div>
        </div>
        <footerContent/>
    </div>
</template>
<script>
import navComponent from './Global/navComponent';
import headerVue from './makemoney/headerVue';
import sectionOne from './makemoney/sectionOne';
import sectionTow from './makemoney/sectionTow';
import footerContent from './Global/footerContent';
export default {
    components:{navComponent,headerVue,sectionOne,sectionTow,footerContent}
}
</script>
<style>
    .notdriveryet{background-color: #43c5db; border-radius: 10px; padding:30px 30px; margin-top: 100px;  margin-bottom: 100px;}
    .titleeee{font-size: 26px; font-weight: 700; font-family: 'Sego'!important; text-align: center; color: white;}
    .notdrivebutton{display: flex; flex-direction: row; justify-content: center; margin-top:30px;}
    .ffsego{font-family: 'Sego'!important; font-weight: 700;}
</style>



